import React, {useEffect,useState, useRef} from 'react'
import styled from 'styled-components'
import anime from 'animejs'
export default function GameOver(props) {
    const [array, setArray] = useState([])
    const REF_ARRAY = useRef(array)
    REF_ARRAY.current = array
    useEffect(()=>{
        let newarray = []
        let gridX = props.width / props.blockSize
        let gridy = props.height / props.blockSize
        for(let i = 0; i < gridX*gridy; i++){
           newarray.push('')
        }
        setArray(newarray)

        
        console.log(REF_ARRAY.current)
    },[])
    useEffect(()=>{
        anime({
            targets: `.block`,
            rotateZ: anime.stagger([0,900],{
                grid: [40,4],
                from: 'center',
                axis: 'x'
            }),
            scale: .3,
            background: `rgba(225,15,225,1)`,
            delay: 300,
            loop: true,
            direction: 'alternate',
            endDelay: 1000,
            easing: `easeInQuad`
        })
    },[array])
    return (
        <GameOverView blockSize={props.blockSize}>
            <div className="flex">
                <h2>GAME OVER</h2>
                <div>{props.eaten}</div>
                <div>FINAL SCORE</div>

            </div>
            <div className="background">
                <div className="grid">

                    {
                        REF_ARRAY.current.map((content, index)=>{
                            return(
                                <div className="block"></div>

                            )

                        })
                    }
                   
                
               </div>
            </div>
        </GameOverView>
    )
}
const GameOverView = styled.div`
    display: flex;
    position: absolute;
    top: 0;
    left: -1px;
    right: -1px;
    bottom: 0;
    z-index: 3;
    flex-direction: column;
    justify-content: space-around;
    font-family: 'Press Start 2P', cursive;
    .flex{
        position: relative;
        z-index: 3;
    }
    .background{
        position: absolute;
        
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

    }
    .grid{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
       
    }
    .block{
        background: rgba(0,0,0,0);
        border: 1px solid rgba(12,123,55,1);
        box-sizing: border-box;
        height: ${props=>`${props.blockSize}px`};
        width: ${props=>`${props.blockSize}px`};

    }
`